/**
 *
 * Главное меню
 *
*/
.menu {
  display: flex;
  align-items: center;
}
.menu--item-left-margin > .menu__item + .menu__item {
  margin-left: 53px;
}
@media screen and (max-width: 1452px) {
  .menu--item-left-margin > .menu__item + .menu__item {
    margin-left: 30px;
  }
}
.menu__item {
  position: relative;
  color: var(--color-white);
  letter-spacing: 0.4px;
  line-height: 22px;
  cursor: pointer;
}
.menu__item--no-hover::after {
  display: none;
}
.menu__item--mobile {
  font-size: 18px;
  margin-top: 5px;
}
.menu__item-span {
  position: relative;
}
.menu__item-span::after {
  content: url('../common/~@img/icons/ic_arrow-more.svg');
  position: absolute;
  width: 20px;
}
.menu__item::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 1px;
  border-radius: 50vh;
  background-color: var(--color-red);
  left: 0;
  bottom: -10px;
  transform-origin: left;
  transform: scaleX(0);
  transition: transform 0.2s;
}
.menu__item:hover::after,
.menu__item--active::after {
  transform: scaleX(1);
}
.menu__item:focus > .menu__inner,
.menu__item:hover > .menu__inner {
  opacity: 1;
  visibility: visible;
}
.menu__inner {
  position: absolute;
  left: -20px;
  top: 100%;
  min-width: 150px;
  padding: 5px;
  z-index: 150;
  background-color: var(--color-background);
  border-radius: 2px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.1s, visibility 0.1s;
}
.menu__inner--mobile {
  left: -100%;
}
.menu__inner-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  border-radius: 2px;
  border: 1px solid var(--color-gray);
  padding: 10px;
}
.menu__inner-wrapper > * + * {
  margin-top: 4px;
}
.header {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  left: 0;
  top: 0;
}
.header--styles {
  width: 100%;
  height: 70px;
  background-color: var(--color-dark);
  z-index: 100;
  transition: background-color 0.5s;
}
.header--styles:after {
  content: '';
  position: absolute;
  bottom: 1px;
  left: 0;
  width: 100vw;
  height: 1px;
  background-color: var(--color-gray);
  transition: opacity 0.5s;
}
@media screen and (max-width: 620px) {
  .header--styles {
    padding: 0 16px;
  }
}
.header--top {
  background-color: transparent;
}
.header--top .header__logo {
  transform: translate(-50%, 116px);
  max-width: 473px;
}
@media screen and (max-width: 620px) {
  .header--top .header__logo {
    left: 50%;
  }
}
.header--top:after {
  opacity: 0;
}
.header__wrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: var(--screen);
  padding: 13px 0 18px;
}
.header__logo {
  position: absolute;
  left: 50%;
  top: 0;
  width: 100%;
  max-width: 147px;
  transform: translate(-50%, 17px);
  transition: transform 0.5s, max-width 0.5s, left 0.5s;
}
.header__logo-img {
  width: 100%;
  object-fit: contain;
  object-position: center;
}
@media screen and (max-width: 1172px) {
  .header__logo {
    transform: translate(-50%, 10px);
  }
}
@media screen and (max-width: 620px) {
  .header__logo {
    max-width: 111px;
    left: 60%;
    transform: translate(-50%, 17px);
  }
}
@media screen and (max-width: 328px) {
  .header__logo {
    max-width: 71px;
    transform: translate(-50%, 19px);
    left: 68%;
  }
}
.header__info {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.header__info-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  color: var(--color-white);
  min-width: 121px;
}
.header__info-wrapper--margin-left-50 {
  margin-left: 50px;
}
@media screen and (max-width: 1452px) {
  .header__info-wrapper--margin-left-50 {
    margin-left: 19px;
  }
}
@media screen and (max-width: 1172px) {
  .header__info-wrapper--margin-left-50 {
    display: none;
  }
}
.header__info-wrapper--margin-left-60 {
  margin-left: 60px;
}
@media screen and (max-width: 1452px) {
  .header__info-wrapper--margin-left-60 {
    margin-left: 29px;
  }
}
@media screen and (max-width: 1172px) {
  .header__info-wrapper--margin-left-60 {
    margin-left: 0;
  }
}
.header__info-wrapper--row {
  flex-direction: row;
}
@media screen and (max-width: 1172px) {
  .header__info-wrapper--row {
    display: none;
  }
}
@media screen and (max-width: 1172px) {
  .header__info-wrapper {
    align-items: flex-start;
  }
}
@media screen and (max-width: 620px) {
  .header__info-wrapper {
    font-size: 12px;
  }
}
@media screen and (max-width: 1172px) {
  .header__nav {
    display: none;
  }
}
/**
 *
 * Подвал сайта
 *
*/
.footer {
  position: relative;
}
.footer--styles {
  width: 100vw;
  background-color: var(--color-dark);
  margin-top: 146px;
}
.footer--styles::before {
  content: '';
  position: absolute;
  left: 0;
  top: 5px;
  width: 100vw;
  height: 1px;
  background-color: var(--color-gray);
}
.footer__wrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: var(--screen);
  padding: 45px 0 15px;
  margin: 0 auto;
}
@media screen and (max-width: 1172px) {
  .footer__wrapper {
    flex-direction: column;
    align-items: center;
    padding: 45px 0 50px;
  }
}
.footer__left {
  flex: 1 1 auto;
  display: flex;
  max-width: 33%;
}
.footer__left-info {
  display: flex;
  flex-direction: column;
}
@media screen and (max-width: 1172px) {
  .footer__left-info {
    align-items: center;
  }
}
.footer__left-logo {
  width: 109px;
  height: 29px;
}
.footer__left-logo-img {
  width: inherit;
  height: inherit;
  object-fit: contain;
  object-position: center;
}
.footer__left-nav {
  display: flex;
  flex-direction: column;
  margin-left: 50px;
}
.footer__left-nav > .footer__left-nav-item + .footer__left-nav-item {
  margin-top: 5px;
}
@media screen and (max-width: 1172px) {
  .footer__left-nav {
    align-items: center;
    margin-top: 21px;
    margin-left: 0;
  }
}
.footer__left-soc {
  margin-top: 35px;
}
@media screen and (max-width: 1172px) {
  .footer__left {
    flex-direction: column;
    align-items: center;
    max-width: 100%;
  }
}
.footer__center {
  flex: 1 1 auto;
  max-width: 408px;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
}
@media screen and (max-width: 1172px) {
  .footer__center {
    margin: 50px 0;
  }
}
.footer__info {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  max-width: 33%;
  color: var(--color-white-dark);
}
.footer__info-tel {
  text-align: center;
}
.footer__info-time {
  margin-top: 1rem;
}
@media screen and (max-width: 1172px) {
  .footer__info {
    max-width: 100%;
    align-items: center;
  }
}
.footer__copyright {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 15px;
}
.footer__copyright-text {
  color: var(--color-white-dark);
  text-align: center;
}
/**
 *
 * Ссылка
 *
*/
.link {
  position: relative;
}
.link--styles {
  color: var(--color-white);
  white-space: nowrap;
}
.link--styles > span {
  position: relative;
}
.link--styles > span::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -1px;
  width: 100%;
  height: 1px;
  background-color: var(--color-white);
  opacity: 0;
  transition: opacity 0.1s;
}
.link--styles::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -1px;
  width: 100%;
  height: 1px;
  background-color: var(--color-white);
  opacity: 0;
  transition: opacity 0.1s;
}
.link--styles:hover::after {
  opacity: 1;
}
.link--styles:hover > span::after {
  opacity: 1;
}
.link--gray {
  color: var(--color-white-dark);
}
.link--gray::after {
  background-color: var(--color-white-dark);
}
.link--gray > span::after {
  background-color: var(--color-white-dark);
}
.link--span::after {
  display: none;
}
.body {
  --color-background: #171717;
  --color-dark: #18181C;
  --color-gray-dark: #2E2E35;
  --color-gray: #3A3A3A;
  --color-gray-light: #66666A;
  --color-white-dark: #D4D4D4;
  --color-white-medium: #E4E4E4;
  --color-white: #F8F8F8;
  --color-red-light: #d13737;
  --color-red: #C81717;
  --color-red-dark: #8A1029;
  --color-green: #0D8220;
  --color-menu-label: rgba(46, 46, 53, 0.8);
  --color-menu-label-active: rgba(46, 46, 53, 0.96);
  --border-radius: 2px;
  --font: 'Nunito', sans-serif;
  --screen: calc(1452px - 12px);
  font-family: var(--font);
  background-color: var(--color-background);
  color: var(--color-white);
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url('../~@src/img/background.jpg');
  overflow-x: hidden;
  width: 100%;
  min-height: 100vh;
}
.body--overflow {
  overflow: hidden;
}
@media screen and (max-width: 1452px) {
  .body {
    --screen: calc(1172px - 12px);
  }
}
@media screen and (max-width: 1172px) {
  .body {
    --screen: calc(620px - 12px);
  }
}
@media screen and (max-width: 620px) {
  .body {
    --screen: 100%;
  }
}
.content {
  width: 100%;
  max-width: var(--screen);
  margin: 0 auto;
}
.content--styles {
  padding-top: 328px;
}
@media screen and (max-width: 620px) {
  .content--styles {
    padding: 328px 16px 0;
  }
}
/**
 *
 * Кнопка (основная)
 *
*/
.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.btn--styles {
  min-width: 242px;
  height: 64px;
  border-radius: 100vh;
  color: var(--color-white);
  font-size: 20px;
  letter-spacing: 0.5px;
  line-height: 27px;
  transition: box-shadow 0.2s;
}
.btn--main {
  background: linear-gradient(90deg, var(--color-red), var(--color-red-dark));
}
.btn--main:hover {
  box-shadow: 0 3px 14px var(--color-red);
}
.btn--stroke {
  border: 1px solid var(--color-red);
  background-color: transparent;
  transition: background-color 0.2s;
}
.btn--stroke:hover {
  background-color: var(--color-red);
}
.btn--min {
  min-width: 131px;
  height: 40px;
  font-size: 14px;
  letter-spacing: 0.35px;
  line-height: 19px;
  font-weight: 300;
}
.btn--padding {
  padding: 0 37px;
}
.btn__icon {
  margin-left: 16px;
}
/**
 *
 * Поле ввода
 *
*/
.input {
  position: relative;
  display: flex;
}
.input--styles {
  width: 100%;
  max-width: 270px;
  height: 33px;
}
.input__text,
.input__password,
.input__email,
.input__textarea {
  position: relative;
  width: 100%;
  margin: 0 auto;
  background: transparent;
  padding: 0 5px;
  border: none;
  border-bottom: 1px solid var(--color-white-dark);
  outline: none;
  color: var(--color-white);
  font-size: 18px;
  font-weight: 300;
}
.input__textarea {
  resize: none;
}
.input__label {
  position: absolute;
  left: 5px;
  bottom: 8px;
  font-size: 18px;
  font-weight: 300;
  color: var(--color-gray-light);
  cursor: text;
  transition: transform 0.1s, font-size 0.1s;
}
.input__label--top {
  bottom: auto;
  top: 8px;
}
.input__border {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: var(--color-red);
  transform-origin: left;
  transform: scaleX(0);
  transition: transform 0.1s;
}
.input__text:focus + .input__label,
.input__password:focus + .input__label,
.input__email:focus + .input__label,
.input__textarea:focus + .input__label,
.input__text:not(:placeholder-shown) + .input__label,
.input__password:not(:placeholder-shown) + .input__label,
.input__email:not(:placeholder-shown) + .input__label,
.input__textarea:not(:placeholder-shown) + .input__label {
  transform: translate(0, -20px);
  font-size: 12px;
}
.input__text:focus ~ .input__border,
.input__password:focus ~ .input__border,
.input__email:focus ~ .input__border,
.input__textarea:focus ~ .input__border {
  transform: scaleX(1);
}
/**
 *
 * Карточка товара
 *
*/
.card {
  position: relative;
  display: flex;
}
.card--styles {
  width: 100%;
  max-width: 351px;
  min-height: 508px;
  border-radius: 2px;
  padding: 5px;
  background-color: var(--color-dark);
}
@media screen and (max-width: 1452px) {
  .card--styles {
    max-width: 278px;
    min-height: 595px;
  }
}
@media screen and (max-width: 1172px) {
  .card--styles {
    max-width: 296px;
    min-height: 606px;
    margin-bottom: 12px;
  }
}
@media screen and (max-width: 620px) {
  .card--styles {
    max-width: 100%;
    min-height: auto;
  }
}
.card--margin-bottom {
  margin-bottom: 12px;
}
.card__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  border: 1px solid var(--color-gray);
  border-radius: 2px;
  padding: 10px 11px 15px;
}
.card__img {
  display: block;
  width: 226px;
  height: 193px;
  object-fit: contain;
  object-position: center;
  margin: 0 auto;
}
.card__title {
  margin-top: 5px;
}
.card__structure {
  min-height: 60px;
  margin-top: 15px;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.38px;
  font-weight: 300;
  color: var(--color-white-dark);
}
.card__options {
  min-height: 41px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 24px;
}
.card__option {
  display: flex;
  flex-direction: column;
  padding-right: 48px;
}
.card__option-price {
  color: var(--color-white-dark);
  transition: color 0.1s;
}
.card__option-description {
  font-weight: 300;
  font-size: 14px;
  color: var(--color-white-dark);
  transition: color 0.1s;
}
.card__option-input {
  display: none;
}
.card__option-label {
  cursor: pointer;
}
.card__option-input:checked ~ label > .card__option-price,
.card__option-input:checked ~ label > .card__option-description {
  color: var(--color-red-light);
}
@media screen and (max-width: 620px) {
  .card__option {
    padding-right: 30px;
  }
}
.card__weight {
  letter-spacing: 0.48px;
  color: var(--color-gray-light);
  margin-top: 29px;
}
.card__footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-top: 10px;
}
@media screen and (max-width: 1452px) {
  .card__footer {
    align-items: flex-start;
  }
}
.card__price {
  font-weight: 600;
  color: var(--color-white);
  font-size: 28px;
  line-height: 28px;
  letter-spacing: 0.84px;
}
.card__ui {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
@media screen and (max-width: 1452px) {
  .card__ui {
    flex-direction: column;
    align-items: flex-end;
  }
}
.card__input {
  display: flex;
  align-items: center;
  margin-right: 20px;
}
.card__input-num {
  font-size: 28px;
  line-height: 28px;
  letter-spacing: 0.84px;
  font-weight: 300;
  color: var(--color-white-dark);
}
.card__input-ui {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  margin-left: 8px;
}
.card__input-btn {
  position: relative;
  width: 18px;
  height: 14px;
  background-color: transparent;
  cursor: pointer;
}
.card__input-btn > span {
  position: absolute;
  display: block;
  top: 0;
  width: 9px;
  height: 1px;
  background-color: var(--color-white-dark);
  transition: transform 0.2s;
}
.card__input-btn > span:first-child {
  transform-origin: right;
  transform: rotate(-36deg);
}
.card__input-btn > span:last-child {
  transform-origin: left;
  left: 50%;
  transform: rotate(36deg);
}
.card__input-btn:hover > span:first-child {
  transform: rotate(-46deg);
}
.card__input-btn:hover > span:last-child {
  transform: rotate(46deg);
}
.card__input-btn:active > span:first-child {
  transform: rotate(-56deg);
}
.card__input-btn:active > span:last-child {
  transform: rotate(56deg);
}
.card__input-btn--bottom {
  transform: scaleY(-1);
}
@media screen and (max-width: 1452px) {
  .card__input {
    margin-right: 0;
    margin-bottom: 14px;
  }
}
