.admin-item-group {
  display: flex;
  flex-direction: column;
}
.admin-item-group--styles {
  width: 100%;
  margin: 100px 0 20px;
}
.admin-item-group--align-items-center {
  align-items: center;
  flex-direction: row;
  padding-left: 30px;
}
.admin-item-group__header {
  position: relative;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding-left: 30px;
}
.admin-item-group__header-btn--margin-right {
  margin-right: 20px;
}
.admin-item-group__input {
  position: relative;
  min-width: 420px;
  margin: 0 40px 0 0;
  font-family: var(--font);
  font-size: 32px;
  color: var(--color-white);
  font-weight: 700;
  letter-spacing: 0.8px;
  background: transparent;
  border: none;
}
.admin-item-group__input::placeholder {
  color: var(--color-gray-light);
  font-weight: 700;
  letter-spacing: 0.8px;
}
.admin-item-group__input-btn {
  max-width: 162px;
  margin-left: 40px;
}
.admin-item-group__input-btn--margin-right {
  margin-right: 20px;
}
@media screen and (max-width: 1172px) {
  .admin-item-group__input {
    margin: 0 40px 20px 0;
  }
}
.admin-item-group__id-group {
  position: absolute;
  right: calc(100% - 20px);
  top: 0;
  color: var(--color-gray-light);
  font-size: 14px;
}
.admin-item-group__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  margin-top: 40px;
}
/**
 *
 * Карточка товара в админ-панеле
 *
*/
.admin-card {
  position: relative;
  z-index: 100;
}
.admin-card--styles {
  width: 100%;
  max-width: 351px;
  min-height: 508px;
  border-radius: 2px;
  padding: 5px;
  background-color: var(--color-dark);
}
@media screen and (max-width: 1452px) {
  .admin-card--styles {
    max-width: 278px;
    min-height: 595px;
  }
}
@media screen and (max-width: 1172px) {
  .admin-card--styles {
    max-width: 296px;
    min-height: 606px;
    margin-bottom: 12px;
  }
}
@media screen and (max-width: 620px) {
  .admin-card--styles {
    max-width: 100%;
    min-height: auto;
  }
}
.admin-card--margin-bottom {
  margin-bottom: 48px;
}
@media screen and (max-width: 620px) {
  .admin-card--margin-bottom {
    width: 100%;
  }
}
.admin-card__main {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
}
.admin-card__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  min-height: inherit;
  border: 1px solid var(--color-gray);
  border-radius: 2px;
  padding: 10px 11px 15px;
}
.admin-card__wrapper-inner {
  width: 100%;
}
.admin-card__wrapper-id {
  position: absolute;
  left: 3px;
  top: 0;
  color: var(--color-gray-light);
  font-size: 14px;
}
.admin-card__wrapper-close {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 8px;
  right: 8px;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.admin-card__wrapper-close > i {
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: var(--color-gray-light);
  transition: background-color 0.1s;
}
.admin-card__wrapper-close > i:first-child {
  transform: rotate(45deg);
}
.admin-card__wrapper-close > i:last-child {
  transform: rotate(-45deg);
}
.admin-card__wrapper-close:hover > i {
  background-color: var(--color-red);
}
.admin-card__img {
  width: 226px;
  height: 193px;
  margin: 0 auto;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  border: 1px solid var(--color-dark);
  transition: border-color 0.1s;
}
.admin-card__img-label {
  display: block;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.admin-card__img-input {
  display: none;
}
.admin-card__img:hover {
  border-color: var(--color-red);
}
.admin-card__name {
  width: 100%;
  font-family: var(--font);
  font-size: 22px;
  line-height: 28px;
  letter-spacing: 0.99px;
  font-weight: 600;
  margin-top: 5px;
  color: var(--color-white);
  background: transparent;
  border: 1px solid transparent;
  overflow: hidden;
  outline: none;
  resize: none;
  transition: border-color 0.1s;
}
.admin-card__name::placeholder {
  color: var(--color-gray-light);
}
.admin-card__name--hide {
  display: none;
}
.admin-card__name:focus {
  border-color: var(--color-red);
}
.admin-card__structure {
  display: block;
  font-family: var(--font);
  width: 100%;
  height: 60px;
  margin-top: 15px;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.38px;
  font-weight: 300;
  color: var(--color-white-dark);
  background: transparent;
  resize: none;
  outline: none;
  border: 1px solid transparent;
  overflow: hidden;
  transition: border-color 0.1s;
}
.admin-card__structure--hide {
  display: none;
}
.admin-card__structure:focus {
  border-color: var(--color-red);
}
@media screen and (max-width: 1452px) {
  .admin-card__structure {
    height: 80px;
  }
}
.admin-card__options {
  display: flex;
  justify-content: space-between;
  width: 100%;
  min-height: 41px;
  margin-top: 24px;
}
.admin-card__option {
  display: flex;
  flex-direction: column;
  max-width: 25%;
}
.admin-card__option-input {
  font-family: var(--font);
  font-size: 16px;
  color: var(--color-white);
  background-color: transparent;
  outline: none;
  border: 1px solid var(--color-dark);
  transition: border-color 0.1s;
}
.admin-card__option-input::placeholder {
  color: var(--color-gray-light);
}
.admin-card__option-input:focus {
  border-color: var(--color-red);
}
.admin-card__option-description {
  font-weight: 300;
  color: var(--color-white-dark);
}
.admin-card__weight {
  font-family: var(--font);
  font-size: 16px;
  letter-spacing: 0.48px;
  color: var(--color-gray-light);
  margin-top: 29px;
  background-color: transparent;
  border: 1px solid transparent;
  overflow: hidden;
  outline: none;
  transition: border-color 0.1s;
  max-width: 100px;
}
.admin-card__weight--hide {
  display: none;
}
.admin-card__weight:focus {
  border-color: var(--color-red);
}
.admin-card__price {
  display: block;
  font-family: var(--font);
  font-weight: 600;
  color: var(--color-white);
  font-size: 28px;
  line-height: 28px;
  letter-spacing: 0.84px;
  margin-top: 7px;
  background: transparent;
  max-width: 150px;
  border: 1px solid transparent;
  overflow: hidden;
  outline: none;
  transition: border-color 0.1s;
}
.admin-card__price::placeholder {
  color: var(--color-gray-light);
}
.admin-card__price--hide {
  display: none;
}
.admin-card__price:focus {
  border-color: var(--color-red);
}
.admin-card__new {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  border: 1px solid var(--color-gray);
  border-radius: 2px;
  padding: 10px 11px 15px;
  cursor: pointer;
}
.admin-card__save-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 40px;
  background-color: var(--color-dark);
  border-radius: 2px 2px 0 0;
  z-index: 0;
  transition: transform 0.2s;
}
.admin-card__save-wrapper::after {
  content: '';
  position: absolute;
  left: 4px;
  top: 4px;
  right: 4px;
  bottom: 4px;
  border: 1px solid var(--color-gray);
  border-radius: 2px;
  pointer-events: none;
  z-index: 3;
}
.admin-card__save-wrapper-button {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 2px;
  border: none;
  background-color: var(--color-green);
  cursor: pointer;
  padding: 5px;
  color: var(--color-white-medium);
}
.admin-card__save-wrapper--show {
  transform: translateY(-100%);
}
/**
 *
 * Страница обработки заказов
 *
*/
.content {
  width: 100%;
  max-width: var(--screen);
}
@media screen and (max-width: 620px) {
  .content {
    padding: 0 16px;
  }
}
.admin-page {
  display: flex;
  flex-direction: column;
}
.admin-page--styles {
  width: 100%;
  padding: 115px 0 80px;
}
